<template>
    <div class="home">
        <section class="py-3 text-center container" ref="heroSection" @mousemove="handleMouseMove">
            <div class="row py-lg-3">
                <div class="col-lg-6 col-md-8 mx-auto">
                    <div class="logo-container">
                        <img :src="'/images/main/mainLogo.png'" alt="ÌîºÏΩúÎ°ú Î°úÍ≥†" class="main-logo" @click="createFireworks">
                    </div>
                    <h1 class="main-title">ÍøÄÏûºÍ∞Å üéÆ</h1>
                    <p class="main-subtitle">Ïã¨Î¶¨ÌÖåÏä§Ìä∏ vs Î∞∏Îü∞Ïä§Í≤åÏûÑ vs Ïó∞ÏòàÏù∏ÌÄ¥Ï¶à... Îã§ ÏûàÏñ¥Ïöî! üéä</p>
                </div>
            </div>

            <!-- Î≥ÑÏ°∞Í∞Å Ìö®Í≥º -->
            <div v-for="(star, index) in stars" :key="'star-' + index" class="star-particle" :style="{
                left: star.x + 'px',
                top: star.y + 'px',
                opacity: star.opacity
            }">
                ‚ú¶
            </div>

            <!-- Ìè≠Ï£Ω Ìö®Í≥º -->
            <div v-for="(firework, index) in fireworks" :key="'firework-' + index" class="firework-particle" :style="{
                left: firework.x + 'px',
                top: firework.y + 'px',
                color: firework.color,
                opacity: firework.opacity,
                transform: `translate(${firework.moveX}px, ${firework.moveY}px) scale(${firework.scale})`
            }">
                {{ firework.symbol }}
            </div>
        </section>

        <div class="album py-3 bg-body-tertiary">
            <div class="container">
                <div class="row row-cols-1 row-cols-sm-2 row-cols-md-3 g-3">
                    <div class="col" v-for="(item, i) in items" :key="i">
                        <Card :item="item" @click="navigation(item.id)"/>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
import Card from '../components/main/Card.vue'
import axios from 'axios'
import { useRouter } from 'vue-router';

export default {
    name: 'Home',
    components: {
        Card
    },
    setup() {
        const router = useRouter();

        const navigation = (id) => {
            router.push(`/mindtest?id=${id}`);
        }

        return {navigation};
    },
    data() {
        return {
            items: [],
            stars: [],
            starId: 0,
            fireworks: [],
            fireworkId: 0,
            fireworkCooldown: false
        };
    },
    methods: {
        fetchItems() {
            axios.get('/api/items')
                .then(response => {
                    this.items = response.data;
                })
                .catch(error => {
                    console.error(error);
                });
        },

        handleMouseMove(event) {
            const rect = this.$refs.heroSection.getBoundingClientRect();
            const x = event.clientX - rect.left;
            const y = event.clientY - rect.top;

            // Î≥ÑÏ°∞Í∞Å ÏÉùÏÑ± (10% ÌôïÎ•†Î°ú)
            if (Math.random() < 0.1) {
                this.createStar(x, y);
            }
        },

        createStar(x, y) {
            const star = {
                id: this.starId++,
                x: x + (Math.random() - 0.5) * 20,
                y: y + (Math.random() - 0.5) * 20,
                opacity: 1
            };

            this.stars.push(star);

            setTimeout(() => {
                const starIndex = this.stars.findIndex(s => s.id === star.id);
                if (starIndex !== -1) {
                    this.fadeOutStar(starIndex);
                }
            }, 100);
        },

        fadeOutStar(index) {
            const fadeInterval = setInterval(() => {
                if (this.stars[index]) {
                    this.stars[index].opacity -= 0.05;

                    if (this.stars[index].opacity <= 0) {
                        this.stars.splice(index, 1);
                        clearInterval(fadeInterval);
                    }
                } else {
                    clearInterval(fadeInterval);
                }
            }, 30);
        },

        // ÏµúÏ†ÅÌôîÎêú Ìè≠Ï£Ω Ìö®Í≥º ÏÉùÏÑ±
        createFireworks(event) {
            // Ïø®Îã§Ïö¥ Ï≤¥ÌÅ¨ (Ïó∞ÏÜç ÌÅ¥Î¶≠ Î∞©ÏßÄ)
            if (this.fireworkCooldown) return;

            this.fireworkCooldown = true;
            setTimeout(() => {
                this.fireworkCooldown = false;
            }, 1000);

            const rect = this.$refs.heroSection.getBoundingClientRect();
            const centerX = event.clientX - rect.left;
            const centerY = event.clientY - rect.top;

            // ÏµúÏ†ÅÌôî: ÏÉâÏÉÅÍ≥º Ïã¨Î≥º Ïàò Ï§ÑÏûÑ
            const colors = ['#FFD700', '#FF6B6B', '#4ECDC4', '#45B7D1'];
            const symbols = ['‚ú®', '‚≠ê', 'üåü', '‚ú¶'];

            // ÏµúÏ†ÅÌôî: Ìè≠Ï£Ω Í∞úÏàò 12Í∞úÎ°ú Ï§ÑÏûÑ
            for (let i = 0; i < 6; i++) {
                const angle = (Math.PI * 2 * i) / 12;
                const velocity = 80 + Math.random() * 60;

                const firework = {
                    id: this.fireworkId++,
                    x: centerX,
                    y: centerY,
                    moveX: 0,
                    moveY: 0,
                    color: colors[Math.floor(Math.random() * colors.length)],
                    symbol: symbols[Math.floor(Math.random() * symbols.length)],
                    opacity: 1,
                    scale: 1,
                    velocityX: Math.cos(angle) * velocity,
                    velocityY: Math.sin(angle) * velocity,
                    gravity: 0.8
                };

                this.fireworks.push(firework);
                this.animateFireworkOptimized(firework);
            }
        },

        // ÏµúÏ†ÅÌôîÎêú Í∞úÎ≥Ñ Ìè≠Ï£Ω Ïï†ÎãàÎ©îÏù¥ÏÖò
        animateFireworkOptimized(firework) {
            let frame = 0;
            const maxFrames = 60; // 0.33Ï¥àÎ°ú Îã®Ï∂ï

            const animate = () => {
                frame += 2; // ÌîÑÎ†àÏûÑ Ïä§ÌÇµÏúºÎ°ú ÏÑ±Îä• Ìñ•ÏÉÅ

                if (frame >= maxFrames) {
                    // Ìè≠Ï£Ω Ï†úÍ±∞
                    const index = this.fireworks.findIndex(f => f.id === firework.id);
                    if (index !== -1) {
                        this.fireworks.splice(index, 1);
                    }
                    return;
                }

                // Î¨ºÎ¶¨ Ìö®Í≥º Ï†ÅÏö©
                firework.velocityY += firework.gravity;
                firework.moveX += firework.velocityX * 0.02;
                firework.moveY += firework.velocityY * 0.02;

                // ÌéòÏù¥ÎìúÏïÑÏõÉ Ìö®Í≥º
                firework.opacity = 1 - (frame / maxFrames);
                firework.scale = 1 + (frame / maxFrames) * 0.3;

                requestAnimationFrame(animate);
            };

            requestAnimationFrame(animate);
        }
    },
    mounted() {
        this.fetchItems();
    }
}
</script>


<style scoped>
